import React, {useEffect, useState} from 'react'
import {api} from "../../Shared/Utilities/api";
import {Link} from "react-router-dom";

export default function GetAllFields() {
  const [fields, setFields] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [offset, setOffset] = useState(0);
  const limit = 15;
  const totalPages = Math.ceil(fields.length / limit);
  const currentPage = Math.floor(offset / limit) + 1;

  useEffect(() => {
    const fetchFields = async () => {
      setLoading(true);
      setError(null);

      try {
        const response = await api.get("/fields");
        setFields(response.data.data);
      } catch (err) {
        setError(err.response?.data?.message || 'Failed to load fields');
      } finally {
        setLoading(false);
      }
    };
    fetchFields();
  }, []);

  const paginatedFields = () => fields.slice(offset, offset + limit);

  const handleNextPage = () => {
    if (offset + limit < fields.length) {
      setOffset(prev => prev + limit);
    }
  };

  const handlePreviousPage = () => {
    if (offset > 0) {
      setOffset(prev => Math.max(prev - limit, 0));
    }
  };

  if (loading) return (
    <div className="loader-container">
      <div className="loader"></div>
    </div>
  );

  if (error) return (
    <div className="error-message">{error}</div>
  );

  return (
    <div className="card">
      <div className="card-header-row">
        <h2 className="card-title">All Fields</h2>
        <div className="pagination-controls">
          <span>Page {currentPage} of {totalPages}</span>
          <button 
            onClick={handlePreviousPage} 
            disabled={offset === 0}
            className="secondary-btn"
          >
            &lt; Previous
          </button>
          <button 
            onClick={handleNextPage} 
            disabled={offset + limit >= fields.length}
            className="secondary-btn"
          >
            Next &gt;
          </button>
        </div>
      </div>
      
      <div className="card-body">
        <div className="table-container">
          <table className="data-table">
            <thead>
              <tr>
                <th>Title</th>
                <th>Duration</th>
                <th>Free Topics</th>
                <th>Access</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {paginatedFields().map((field) => (
                <tr key={field.id}>
                  <td>{field.title}</td>
                  <td>{field.years_length} year(s)</td>
                  <td>{field.number_of_free_topics}</td>
                  <td>
                    {field.isFree ? 
                      <span className="free-access">Free</span> : 
                      <span className="premium-access">Premium</span>
                    }
                  </td>
                  <td>
                    <Link 
                      to={`/fields/get/${field.id}`} 
                      className="action-btn view-btn"
                    >
                      View Details
                    </Link>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          
          {fields.length === 0 && (
            <div className="empty-state">
              <p>No fields available</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}